import moment from 'moment';
//FILTERS REDUCER

const filtersReducerDefaultState = {
    text: "",
    sortBy: 'date',
    startDate: moment().startOf('month'),
    endDate: moment().endOf('month')
  };
  
  export default (state = filtersReducerDefaultState, action) => {
    switch (action.type) {
      case "SET_TEXT_FILTER":
        return {
          ...state.expenses,
          text: action.text
        };
      case "SORT_BY_DATE":
        return {
          ...state.filters,
          sortBy: "date"
        };
  
      case "SORT_BY_AMOUNT":
        return {
          ...state.filters,
          sortBy: "amount"
        };
  
      case "SET_START_DATE":
        return {
          ...state,
          startDate: action.startDate
        };
      case "SET_END_DATE":
        return {
          ...state,
          endDate: action.endDate
        };
      default:
        return state;
    }
  };


